.lang-dropdown{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: relative;
}

.lang-dropdown-button{
    background: none;
    border: none;
    height:35px;
    border-radius: 4px;
    width: 70px;
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 
                0px 2px 2px 0px rgba(0,0,0,0.14), 
                0px 1px 5px 0px rgba(0,0,0,0.12);
    opacity: 1;
    cursor: pointer;
    color: #D1D1D1;
}

.lang-dropdown-button:hover{
    color: rgb(255, 255, 255);
}

.lang-dropdown-button:active{
    opacity: 0.7;
}

.lang-dropdown-button:focus{
    outline: none;
    color: rgb(255, 255, 255);
}

.lang-dropdown-menu{
    display: flex;
    flex-direction: column;
    position:absolute;
    border: none;
    width: 70px;
    height:20px;
}

.lang-dropdown-menu-button{
    width: 100%;
    padding: 0;
    margin: 0;
    border: none;
    margin-top: 3px;
    padding: 5px;
    opacity: 1;
    border-radius: 3px;
    background: none;
    color: #D1D1D1;
    cursor: pointer;
}

.lang-dropdown-menu-button:hover{
    color: #ffffff;
}

.lang-dropdown-menu-button:active{
    opacity: 0.7;
}

.lang-dropdown-menu-button:focus{
    outline: none;
}

/* RESPONSIVE QUERIES */

@media screen and (max-width: 480px) {
    .lang-dropdown-button{
        width: 150px;
    }

    .lang-dropdown-menu{
        width: 150px;
    }
}

