@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Assistant&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', 'Assistant', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/* RESPONSIVE QUERIES */

@media screen and (min-width: 480px) {

  .app::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: #F5F5F5;
  }

  .app::-webkit-scrollbar{
    width: 6px;
    background-color: #F5F5F5;
  }

  .app::-webkit-scrollbar-thumb{
    background-color: #202020;
  }

  .app{
    scrollbar-color: rgba(0, 0, 0, 0.4) rgba(201, 201, 201, 0);
    scrollbar-width: thin;
  }
}
.app{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(6, auto) 1fr;
    height: 100vh;
    align-content: flex-start;
    overflow: scroll;
    overflow-x: hidden;
}

.app-header-container{
    padding-top: 20px;
    display: flex;
    grid-column: 1;
    grid-row: 1;
    height: auto;
    justify-content: center;
    align-content: center;
    background: black;
}

.app-banner-container{
    display: flex;
    grid-column: 1;
    grid-row: 2;
    height: auto;
    justify-content: center;
    align-content: center;
    background: black;
}

.app-categories-container{
    display: flex;
    flex-direction: row;
    grid-column: 1;
    grid-row: 3;
    height: 40px;
    background: #232323;
}

.app-days-container{
    display: flex;
    grid-column: 1;
    grid-row: 4;
    margin-top: 15px;
    justify-content: center;
}

.days-panel-slider-arrows{
    display: none;
}

.app-feed-container{
    display: flex;
    flex-direction: column;
    grid-column: 1;
    grid-row: 5;
    background: #F9F9F9;
    border-radius: 10px;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 30px;
    padding-top: 50px;
    margin-bottom: 30px;
}

.app-footer-container{
    display: flex;
    grid-column: 1;
    grid-row: 6;
    padding: 10px;
    margin-right: -30px;
    margin-left: -30px;
    justify-content: center;
}

.app-scrollup-button {
    position: fixed; 
    bottom: 20px;
    right: 20px;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    color: rgb(0, 0, 0);
    font-size: 40pt;
    opacity: 0.2;
  }
  
  .app-scrollup-button:hover{
    opacity: 1;
  }
  
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }

/* RESPONSIVE QUERIES */

@media screen and (max-width: 480px) {
  
    .app{
        padding: 0;
    }

    .app-header-container{
        padding-bottom: 15px;
    }
        

    .app-categories-container{
        border-radius: 0;
        background: none;
        margin-top: 0;
        height: 75px;
    }
    .app-days-container{
        position: relative;
    }

    .app-feed-container{
        margin-top: 15px;
        padding: 0;
        border-radius: 0;
        background: none;
    }

    .days-panel-slider-arrows{
       display: flex;
       justify-content: space-between; 
       position: absolute;
       width: 95vw;
       box-sizing: border-box;
       transform: translate(-50%, -50%);
       top: 50%;
       left: 50%;
       color: white;
       opacity: 0.2;
    }

    .app-footer-container{
        display: none;
    }

}
