/* VvV Custom Radio Buttons VvV */

.modal-form-categories-radio-design {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 15px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }
  
  /* Hide the browser's default radio button */
  .modal-form-categories-radio-design input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  
  /* Create a custom radio button */
  .radio-checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: rgb(190, 190, 190);
    border-radius: 50%;
  }
  
  /* On mouse-over, add a grey background color */
  .modal-form-categories-radio-design:hover input ~ .radio-checkmark {
    background-color: rgb(140, 140, 140);
  }
  
  /* When the radio button is checked, add a blue background */
  .modal-form-categories-radio-design input:checked ~ .radio-checkmark {
    background-color: #2196F3;
  }
  
  /* Create the indicator (the dot/circle - hidden when not checked) */
  .radio-checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the indicator (dot/circle) when checked */
  .modal-form-categories-radio-design input:checked ~ .radio-checkmark:after {
    display: block;
  }
  
  /* Style the indicator (dot/circle) */
  .modal-form-categories-radio-design .radio-checkmark:after {
       top: 6px;
      left: 6px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background: white;
  }

