.feed-panel{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr;
}

.feed-panel-title{
    display: grid;
    grid-column: 1;
    grid-row: 1;
}

.feed-panel-title-items{
    display: grid;
    grid-column: 1;
    grid-row: 2;
    font-weight: 300;
    
}

.feed-panel-filter{
    display: grid;
    grid-column: 2;
    grid-row: 1;
    justify-content: center;
    align-self: center;
}

.feed-scroll-container{
    display: grid;
    grid-column: 1/4;
    grid-row: 2;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
}

.feed-item-container{
    display: grid;
    grid-column: 1;
    grid-row: 1;
    grid-template-columns: repeat(auto-fit, 364px);
    grid-gap: 30px;
    gap: 30px;
    box-sizing: border-box;
    justify-content: center;
}

/* RESPONSIVE QUERIES */

@media screen and (max-width: 480px) {
    .feed-item-container{
        grid-gap: 10px;
        gap: 10px;
    }

    .feed-panel-title{
        display: grid;
        grid-column: 1/4;
        justify-self: center;
        padding-top: 10px;
    }

    .feed-panel-title-items{
        grid-column: 1/4;
        justify-self: center;
        padding-bottom: 10px;
    }

    .feed-item-container{
        grid-template-columns: repeat(auto-fit, 100%);
    }

    .feed-scroll-container{
        display: grid;
        grid-column: 1/4;
        grid-row: 3;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }
}
.loading-overlay{
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: rgb(0,0,0);    
}

.loading-overlay img{
  display:flex;
  width: 400px;

}

.loader-container{
  display: flex;
  height: 100px;
}

.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.loader {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


/* RESPONSIVE QUERIES */

@media screen and (max-width: 480px) {
  
  .loading-overlay img{
    display:flex;
    width: 300px;

  }

  .loader-container{
    display: flex;
    height: 50px;
    margin-bottom: 100px;
  }
}
